<template>
  <div class="page">
    <FormSelectGroup v-on:submit-query="submitshow" :module="'reportreceive'" /> 
    <CommonSuspend class="content-area" :suspendobj="suspendOpt" v-on:select-dialog="selectDialog">
      <div class="underlinebtn">
          <!-- <el-button type="primary" size="small" @click="share">分享</el-button> -->
      </div>
      <Scrollframe class="content-frame" :isLastPage="isLastPage"  v-on:ajax-query="ajaxshow" >
          <div class="receivelist"  @mousedown="popclick(item)" v-for="(item,index) in items" :key="index">
              <div class="list-top" :style="{background:item.bgcolor}">
                  <span :title="item.receiveReportTitle">{{item.receiveReportTitle}}</span>
              </div>    
              <div class="list-bot">
                  <span :title="item.publishUserName">{{item.publishUserName}}</span>
                  <span style="margin-bottom:5%;">{{item.usefulTime}}</span>
                  <el-button @click="replychange('1',item)" class="define" size="medium" type="primary" plain v-show="item.isReply">已回复</el-button>
                  <el-button @click="replychange('0',item)" size="medium" type="primary" plain v-show="!item.isReply">回复</el-button>
              </div>    
              <div class="phone">
                <img src="@/assets/img/user.png" width="100%" height="100%"  alt="">
              </div>
          </div>
      </Scrollframe>
    </CommonSuspend>
     <Dialog  :dialogOpt="dialogOpt" v-on:handlecancle-dialog="selectDialog" v-on:handlesure-dialog="submitDialog"></Dialog> 
  </div>
</template>

<script>
import FormSelectGroup from "@/components/formSelectGroup.vue"; //select选择组
import CommonSuspend from "@/components/commonSuspend.vue"; //通用悬浮
import Scrollframe from "@/components/Scrollframe.vue"; //滚动下拉加载
import Dialog from "./dialog.vue"; //本页所有弹窗内容集合(每个页面单独添加)
import server from "./server.js"; //本页所有接口内容集合(每个页面单独添加)
export default {
  data() {
    return {
      //必须配置项
      isLastPage: false, //最后一页
      pagepation: {
        //翻页配置
        pageNum: 1,
        pageSize: 15
      },
      serverobject: server, //ajax服务
      items: [], //列表内容数据

      backgroundColor: [
        //颜色循环
        "linear-gradient(#6a89d9,#4c65a4)",
        "linear-gradient(#f56986,#c77081)",
        "linear-gradient(#86e6d8,#59ada1)"
      ],
      dialogOpt:{
          "dialogflag": false,
          "width":"536px",
      },
      suspendOpt:{
        name:"报告接收",  //唯一区别遮罩的html内容（报告发布的全部展示和报告查看一样）
        position:"top",  //遮罩部件在位置
        domClass:"receivelist",  //遮罩部件
        offsetbottom:"10%",    //遮罩层距离底部的距离
        show:true,       //遮罩层是否显示控件
        bgshow:false,
      },
      isreplayobj:{},
      reports:[],   //勾选的报告对象集合
      reportsindex:{},    //点击的报告对象
    };
  },
  components: {
    FormSelectGroup,
    Scrollframe,
    CommonSuspend,
    Dialog
  },
  methods: {
    popclick(item){   //点击的报告对象
      this.reportsindex = item;
    },
    replychange(val,item){
      let self = this;
      self.isreplayobj = item;
      if(val == "0"){
        self.selectDialog("回复")
      }
    },
    ajaxshow: function(message) {
      //ajax请求数据
      let self = this;
      this.pagepation = message ? message : this.pagepation; //翻页
      self.serverobject.pagepation = this.pagepation; //翻页配置
      server.reportlist(message, function success(res) {
        //接口请求
        let data = res.data.data;
        self.items = data.list;
        self.isLastPage = data.isLastPage; //是否最后一页
        let first = 0,
          second = 1,
          three = 2;
        for (let i = 0; i < self.items.length; i++) {
          let bgcolor = self.backgroundColor, //定义的背景颜色集合
            currentbgcolor = ""; //当前索引的背景颜色
          if (first == i) {
            currentbgcolor = bgcolor[0];
            first = first + 3; //隔三个循环一次
          }
          if (second == i) {
            currentbgcolor = bgcolor[1];
            second = second + 3;
          }
          if (three == i) {
            currentbgcolor = bgcolor[2];
            three = three + 3;
          }
          self.items[i].bgcolor = currentbgcolor;
        }
      });
    },
    selectDialog: function(message, type) {
      //弹窗打开/关闭（message为悬浮组件里的type字段）
      if (type) {
        //关闭弹窗
        this.dialogOpt.dialogflag = false;
      } else {
        //打开弹窗
            this.dialogOpt= {
              "dialogflag":true,     //根据flag决定弹窗是否出现
              "width":"536px",
              "title":message       //根据title决定弹窗页面内容。
            }
      }
    },
    submitshow: function(message) {
      //列表查询条件提交
      this.pagepation = {   //翻页重置
        pageNum: 1,
        pageSize: 15
      },
      this.items=[];
      let messages = Object.assign(message, this.pagepation);      
      this.ajaxshow(messages)      
    },
    submitDialog:function(message,type,formdata){    //对话框保存(formdata为对话框表单对象)
      let self=this;
      let formdatas=formdata;
      if(message == "删除"){
          let params = self.reportsindex.publishId;
          server.reportreceivesdelete(params,function success(res) {  //接口请求、
            if(res.data.status = "SUCCESS"){
                self.dialogOpt.dialogflag=false;
                self.items=[];
                // self.ajaxshow()
                // self.switchChange("已发布")
            }
          });
      }      
    },        
  }
};
</script>

<style scoped lang="less">
.content-frame {
  border-radius: 10px;
  background-color: #ffffff;
  width: 100%;
  min-height: 300px;
  margin-top: -40px;
  padding: 40px 20px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  .receivelist {
    min-width: 320px;
    min-height: 460px;
    width: 3.05rem;
    height: calc(3.05rem + 140px);
    margin: 17px 0px;
    background-color: #f4f4f4;
    box-shadow: 0px 0px 7px 3px rgba(0, 0, 0, 0.09);
    float: left;
    position: relative;
    overflow: hidden;
    .list-top {
      height: 50%;
      text-align: center;
      color: #ffffff;
      padding-top: 50px;
      span {
        font-size: 26px;
        font-weight: normal;
        font-stretch: normal;
        letter-spacing: 0px;
        color: #ffffff;
      }
    }
    .list-bot {
      width: 100%;
      height: 50%;
      text-align: center;
      background: #f4f4f4;
      padding: 30% 5% 0 5%;
      span {
        display: block;
      }
      span:nth-child(1) {
        font-size: 18px;
        color: #1f1f1f;
        margin-bottom: 10px;
      }
      span:nth-child(2) {
        width: 100%;
        height: 40px;
        font-size: 16px;
        color: #666666;
      }
    }
    .phone {
      display:none;
      width: 40%;
      height: calc(40% - 56px);
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      border-radius: 100%;
      overflow: hidden;
      box-shadow: 0px 0px 10px #4c4343;
      img {
        position: absolute;
        top: 0;
        z-index: 2;
        border-radius: 50%;
        border: 5px solid transparent;
      }
    }
    .phone:after {
      content: "";
      position: absolute;
      width: 100%;
      height: 50%;
      background-color: #6988db;
    }
    .phone:before {
      content: "";
      position: absolute;
      width: 100%;
      height: 50%;
      top: 50%;
      background-color: #ffffff;
    }
  }
}
</style>



